import { Injectable } from '@angular/core';
import { Observable } from '../../../node_modules/rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Group } from '../class/group';
import { GroupSearchURL } from '../data-config/mock-datets';
import { httpOptions } from '../data-config/mock-datets';
import { LogService } from './log.service';

@Injectable()
export class GroupService {

  private groupSearchURL = GroupSearchURL;

  constructor(
    private http: HttpClient,
    private logService: LogService,
  ) { }

  searchGroupsByProvince(province: string): Observable<Group[]> {
    if (!province.trim()) {
      return Group[0];
    }

    return this.searchGroups('province=' + province);
  }

  searchGroupsByProvinceSchool(province: string, schoolId: number): Observable<Group[]> {
    if (!province.trim() && schoolId == 0) {
      return Group[0];
    }

    return this.searchGroups('province=' + province + '&schoolId=' + schoolId);
  }

  searchGroupsByProvinceSchoolProfessional(province: string, schoolId: number, professionalId: number): Observable<Group[]> {
    if (!province.trim() && schoolId == 0 && professionalId == 0) {
      return Group[0];
    }

    return this.searchGroups('province=' + province + '&schoolId=' + schoolId + '&professionalId=' + professionalId);
  }

  searchGroups(suffix: string): Observable<Group[]> {
    if (!suffix.trim()) {
      return Group[0];
    }

    return this.http.get<Group[]>(this.groupSearchURL + '?' + suffix)
      .pipe(
        tap(groups => this.logService.log(`fetched groups`)),
        catchError(this.logService.error('getGroups', []))
      );
  }

}



