import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { PROVINCES, GRADES} from '../../data-config/mock-datets';
import { SchoolService } from '../../service/school.service';
import { School } from '../../class/school';
import { Professional } from '../../class/professional';
import { ProfessionalService } from '../../service/professional.service';
import { InformationService } from '../../service/information.service';
import { Information } from '../../class/information';

@Component({
  selector: 'app-information',
  templateUrl: './information.component.html',
  styleUrls: ['./information.component.css']
})
export class InformationComponent implements OnInit {

  provinces: string[];
  schools: School[];
  professionals: Professional[];
  grades: number[];
  informations: Information[];
  selectInfoScope: SelectInfoScope;

  constructor(
    private route: ActivatedRoute,
    private schoolService: SchoolService,
    private professionalService: ProfessionalService,
    private informationService: InformationService,
    private location: Location,
  ) { }

  ngOnInit() {
    this.provinces = PROVINCES;
    this.grades = GRADES;
    this.newSelectInfoScope();
  }

  newSelectInfoScope(): void {
    this.selectInfoScope = {
      schoolId: 0, professionalId: 0, 
      year: (new Date().getFullYear()), 
      name: ''};
  }

  provinceChange(province: string): void {
    this.selectInfoScope.schoolId = 0;
    this.selectInfoScope.professionalId = 0;
    this.professionals = null;

    province = province.trim();
    if (!province) {
      return;
    }

    this.schoolService.getSchoolByProvince(province)
    .subscribe(schools => {
      this.schools = schools;
    });
  }

  schoolChange(id: number): void {
    this.selectInfoScope.professionalId = 0;
    if (id <= 0)
      return;

    this.schoolService.getSchoolById(id)
      .subscribe(school => {
        this.professionals = school.professionals;
      });
  }

  onSubmit() { 
    this.informationService.getInformation(this.selectInfoScope.name, this.selectInfoScope.schoolId, this.selectInfoScope.professionalId, this.selectInfoScope.year)
    .subscribe(informations => {
      this.informations = informations;
    });
    //console.log("succcess take");
  }
}

export class SelectInfoScope {
  schoolId: number;
  professionalId: number;
  year: number;
  name: string;
}


