import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { PROVINCES, GRADES } from '../../data-config/mock-datets';
import { SchoolService } from '../../service/school.service';
import { School } from '../../class/school';
import { Professional } from '../../class/professional';
import { ProfessionalService } from '../../service/professional.service';
import { Teacher } from '../../class/teacher';
import { TeacherService } from '../../service/teacher.service';

@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.css']
})
export class TeacherComponent implements OnInit {

  provinces: string[];
  schools: School[];
  professionals: Professional[];
  grades: number[];
  teacher: Teacher;
  info: string;

  constructor(
    private route: ActivatedRoute,
    private schoolService: SchoolService,
    private professionalService: ProfessionalService,
    private location: Location,
    private teacherService: TeacherService,
  ) { }

  ngOnInit() {
    this.provinces = PROVINCES;
    this.grades = GRADES;
    this.newTeacher();
  }

  newTeacher():void{
    this.teacher = {id: 0, name: '',
    schoolId: 0, professionalId: 0,
    telePhone: '', qq: '', weixin: '',
    requirement: '', score: 0, grade: new Date().getFullYear()};
  }

  provinceChange(province: string): void {
    this.teacher.schoolId = 0;
    this.teacher.professionalId = 0;
    this.professionals = null;
    
    province = province.trim();
    if (!province) {
      return;
    }

    this.schoolService.getSchoolByProvince(province)
      .subscribe(schools => {
        this.schools = schools;
      });
  }

  schoolChange(id: number): void {
    this.teacher.professionalId = 0;

    if (id <= 0)
      return;

    this.schoolService.getSchoolById(id)
      .subscribe(school => {
        this.professionals = school.professionals;
      });
  }

  refresh(): void {
    //this.location.go(this.location.path(true));
    this.info = '信息我们已经获知，请耐心等待！！';
   
    setTimeout(function () {
      window.location.reload();
    }, '3000');
  }

  onSubmit() { 
    console.log(JSON.stringify(this.teacher)); 
    this.teacherService.updateTeacher(this.teacher)
    .subscribe(() => this.refresh());
    //console.log("succcess take");
  }

}



