import { Component, OnInit, Input, group } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { PROVINCES } from '../../data-config/mock-datets';
import { SchoolService } from '../../service/school.service';
import { School } from '../../class/school';
import { Professional } from '../../class/professional';
import { ProfessionalService } from '../../service/professional.service';
import { Group } from '../../class/group';
import { GroupService } from '../../service/group.service';

@Component({
  selector: 'app-group',
  templateUrl: './group.component.html',
  styleUrls: ['./group.component.css']
})

export class GroupComponent implements OnInit {

  provinces: string[];
  schools: School[];
  professionals: Professional[];
  selectGroupScope: SelectGroupScope;
  groups: Group[];

  constructor(
    private route: ActivatedRoute,
    private schoolService: SchoolService,
    private professionalService: ProfessionalService,
    private groupService: GroupService,
    private location: Location,
  ) { }

  ngOnInit() {
    this.provinces = PROVINCES;
    this.SelectGroupScope();
  }

  SelectGroupScope():void{
    this.selectGroupScope = {province: '', schoolId: 0, professionalId: 0};
  }

  provinceChange(province: string): void {

    this.selectGroupScope.schoolId = 0;
    this.selectGroupScope.professionalId = 0;
    this.professionals = null;

    province = province.trim();
    if (!province) {
      return;
    }

    this.schoolService.getSchoolByProvince(province)
      .subscribe(schools => {
        this.schools = schools;
      });

    this.groupService.searchGroupsByProvince(province)
      .subscribe(groups => {
        this.groups = groups;
      });
  }

  schoolChange(id: number): void {
    this.selectGroupScope.professionalId = 0;
    
    this.schoolService.getSchoolById(id)
      .subscribe(school => {
        this.professionals = school.professionals;
      });

    this.groupService.searchGroupsByProvinceSchool(this.selectGroupScope.province, id)
      .subscribe(groups => {
        this.groups = groups;
      });
  }

  professionalChange(id: number): void {
    this.groupService.searchGroupsByProvinceSchoolProfessional(this.selectGroupScope.province, this.selectGroupScope.schoolId, id)
      .subscribe(groups => {
        this.groups = groups;
      });
  }
}

export class SelectGroupScope {
  province: string;
  schoolId: number;
  professionalId: number;
}


