import { Injectable } from '@angular/core';
import { Observable } from '../../../node_modules/rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { httpOptions } from '../data-config/mock-datets';
import { LogService } from './log.service';
import { InformationSearchURL } from '../data-config/mock-datets';
import { Information } from '../class/information';

@Injectable()
export class InformationService {

  private informationSearchURL = InformationSearchURL;
  suffix: string;

  constructor(
    private http: HttpClient,
    private logService: LogService,
  ) { }

  getInformation(name: string, schoolId: number, professionalId: number, year: number): Observable<Information[]> {

    this.suffix = '';

    if (name.length > 0)
      this.suffix = 'name=' + name;

    if (schoolId > 0)
    {
      if (this.suffix.length > 0)
        this.suffix = this.suffix + '&schoolId=' + schoolId;
      else
        this.suffix = 'schoolId=' + schoolId;
    }

    if (professionalId > 0)
    {
      if (this.suffix.length > 0)
        this.suffix = this.suffix + '&professionalId=' + professionalId;
      else
        this.suffix = 'professionalId=' + professionalId;
    }

    if (year > 0)
    {
      if (this.suffix.length > 0)
        this.suffix = this.suffix + '&year=' + year;
      else
        this.suffix = 'year=' + year;
    }

    if (this.suffix.length > 0)
     this.suffix = '?' + this.suffix;

    return this.http.get<Information[]>(this.informationSearchURL + this.suffix)
      .pipe(
        tap(informations => this.logService.log(`fetched information`)),
        catchError(this.logService.error('getInformation', []))
      );
  }
}


